.slick-prev,
.slick-next {
  width: 70px;
  height: 70px;
  z-index: 3333;
}

.slick-prev {
  left: 200px;
}

@media screen and (max-width: 1199px) {
  .slick-prev {
    left: 100px;
  }
}

@media screen and (max-width: 480px) {
  .slick-prev {
    left: 20px;
  }
}

.slick-prev:before {
  content: '>';
  background-image: url("../img/rectangle-21-copy-2.png");
  background-repeat: no-repeat;
  -webkit-background-size: 100%;
  background-size: 100%;
  color: transparent;
  width: 70px;
  height: 70px;
}

.slick-next {
  right: 200px;
}

@media screen and (max-width: 1199px) {
  .slick-next {
    right: 100px;
  }
}

@media screen and (max-width: 480px) {
  .slick-next {
    right: 20px;
  }
}

.slick-next:before {
  content: '<';
  background-image: url("../img/rectangle-21.png");
  background-repeat: no-repeat;
  -webkit-background-size: 100%;
  background-size: 100%;
  color: transparent;
  width: 70px;
  height: 70px;
}

.slider div img {
  width: 100%;
}
